import { z } from 'zod';
import { FilterInterface, FilterSchema } from './filter.interface';

export const WhenItemSchema = z.intersection(
  FilterSchema,
  z.object({
    label: z.string().min(1),
    description: z.string().min(1).optional(),
  }),
);

export interface WhenItemInterface extends FilterInterface {
  label: string;
  description?: string;
}
